body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  background-color: #56f3db;
  color: #222;
}

.card .front h2,
.card .inside h2 {
  font-family: 'Lobster', cursive;
  margin: 0 0 10px;
  text-align: center;
}

.card .inside p,
.card .front p {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  text-align: center;
}


.background-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;        /* сохраняет пропорции и заполняет весь экран */
  z-index: -1;
  pointer-events: none;     /* позволяет кликать сквозь видео */
  transition: opacity 0.8s ease-in-out;
}


.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: sans-serif;
  transition: padding 0.5s ease;
}

.card-container.opened {
  justify-content: flex-start;
}

.card {
  width: 300px;
  height: 150px; /* начальная высота */
  margin: 20px auto;
  perspective: 1000px;
  position: relative;
  transition: height 0.5s ease; /* плавная анимация */
}

.card.open {
  height: 320px;
  margin-bottom: 150px;/* при открытии */
}

.card .front, .card .inside {
  width: 100%;
  height: 100%;
  background-color: #ef70df;
  border: 2px solid #1a1919;
  border-radius: 10px;
  position: absolute;
  backface-visibility: hidden;
  transition: transform 0.8s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card .front {
  transform: rotateY(0deg);
  z-index: 2;
}

.card .inside {
  transform: rotateY(180deg);
  background-color: #f18ee2;
  z-index: 1;
}

.card.open .front {
  transform: rotateY(-180deg);
}

.card.open .inside {
  transform: rotateY(0deg);
  z-index: 2;
}

button {
  background-color: #3fd7c5;
  padding: 10px 20px;
  font-size: 16px;
  margin-top: 90px; /* появится отступ только при открытии */
  cursor: pointer;
  transition: margin 0.4s ease;
}
